*{
    padding: 0%;
    margin: 0%;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }


  .footer--container{
    height: auto;
    width: 100%;
    background-color: #222222;
    color: azure;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.contacts, .social, .about, .newsletter{
    height: auto;
    width: 270px;
}
h3{
    font-weight: 500;
}
ul{
    padding: 10px;
    list-style-type: none;
}
li{
    display: flex;
    flex-direction: row;
}
a{
    color: azure;
    text-decoration: dashed;
}
a:active{
    color: azure;
}
a:visited{
    color: azure;
}
a:hover{
    color: azure;
}
.partition{
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    justify-content: space-around;
    /* padding: 15px 0px; */
}

.partition>div{
    /* width: 50%; */
    overflow-wrap: normal;
    margin: 10px;
}

.social>ul{
    display: flex;
    flex-direction: row;
    gap: 15px;
}

input{
    margin: 2px 0px;
    padding: 10px;
    border: none;
    border-radius: 3px;
}