*{
    padding: 0%;
    margin: 0%;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }


  .gallery--container{
    height: auto;
    width: 100%;
    overflow: clip;
}

.gr{
  height: 380px;
  width: 20%;
  filter: grayscale(0.9);
  margin: 4px;
  object-fit: cover;
  object-position: center;
  border-radius: 3px;
}
.clr{
  height: 380px;
  width: 20%;
  margin: 4px;
  object-fit: cover;
  object-position: center;
  border-radius: 3px;
}
.parallax-inner{
  display: flex;
  flex-wrap: nowrap;
  object-fit: cover;
  height: auto;
  width: 1200px;
  object-position: center;
}