*{
    padding: 0%;
    margin: 0%;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.mobile--navigation{
    display: none;

}
.navigation>ul{
    width: 100%;
    display: flex;
    flex-direction: row;
    gap: 8%;
    justify-content: end;
    text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.952);
}

@media (max-width: 800px) {
    .navigation{
        display: none;
    }

    .mobile--navigation{
        position: absolute;
        top: 2%;
        right: 2%;
        display: flex;
        padding: 35px 5px;
        align-items: end;
    }
    .mobile--navigation>ul{
        padding: 0%;
        background-color: rgba(255, 255, 255, 0.753);
        padding: 20px;
        border-radius: 8px;
    }
    .mobile--navigation>ul>li{
        margin: 8px 0px;

    }
    .mobile--navigation a:visited{
        color: black;
        border-bottom: 1px solid grey;
    }
    .mobile--navigation a:link{
        color: black;
        border-bottom: 1px solid grey;
    }
    .dots{
        position: absolute;
        right: 10%;
        top: 3%;
        background: white;
        border-radius: 50%;
        font-size: 28px;
    }
}